<template>
  <div class="info-container" >
    <div class="info-div" >
      <div class="info-header info-header-operate" style="right:0;left:0;top:0">
        <div class="operate-div" style="float:left;padding:0.2em 0;font-size: 1em;">
          <button class="button-operate" @click="readMeterNoClick" style="margin-right:0;" v-if="lastPayData.localControl && lastPayData.localControl == 1">
            <i class="hasIcon"  style="margin-top: 0;"><icon name="fax"  style="font-size: 1.4em;"></icon></i>
            读卡
         </button>
        </div>
        <div class="operate-div" style="margin-right:0;padding:0.2em 0">
       <!--    <template v-for="(item, $index) in FormOpt"> -->
            <combine :option="combineOption" @getSelects="searchLastPay" style="margin-right:0"></combine>
         <!--  </template> -->
          <flash style="float: right;margin-top: 0.5em;font-size: 0.94em;"></flash>
        </div>
      </div>
      <VuePerfectScrollbar class="info-container detail-container"  style="margin-top: 2.8em;height:calc(100% - 3em)">
        <div class="list-content special-content"  >
          <div class="content-left" v-if="operateFlag">
             <p class="HF-p">
               <span>当前账户余额</span>
              <i class="hasIcon hasImg" @click="reflshPayInfo(true)" style="margin-right:0">
                 <img src="dist/img/costControl/fresh.png" style="height:auto" />
               </i>
             </p>
            <p class="content-p" style="margin-bottom:1em">
              <span>{{typeof lastPayData.remainAmount == 'undefined' ? '--' : lastPayData.remainAmount.toString()}}</span>元
            </p>
            <p>截止时间：{{lastPayData.amountCollectTime}}</p>
          </div>
          <div class="content-right" :style="{width: operateFlag ? 'calc(84% - 9.9em)' : 'calc(100% - 4em)'}">
            <ul class="form-list" style="margin-bottom:0.5em" :style="{ 'border-bottom': (lastPayData.isAccount === 2 && lastPayData.isEnabled == 1) ? '1px solid rgba(255,255,255,0.15)' : 'none'}">
              <li v-for="(item, $index) in pageViewData.customerInfo" v-if="item.isShow">
                <span class="label-span">{{item.name}}</span>
                <span class="label-show " style="text-align: left;margin-left: 0em;" v-html="'： &nbsp;' + (item.formatFunc ? formatFuncs[item.formatFunc](item.value,item): getValue(item.value))"></span>
              </li>
            </ul>
            <ul class="form-list" v-if="operateFlag">
              <li style="width:100%">
                <span class="label-show" style="line-height: 3.2;height:3.2em;margin-left:1em">充值金额： &nbsp;</span>
                <input :disabled="chargeFormParam.thisOptFlag!==1"  class="payInput inputStyle" type="text" maxLength = "7"
                    v-model="payInfo.payMoney" @blur="inputFocus" @keyup="keyUp"/>
                <!--v-if="controls[11]"  v-if="controls[12]"-->
                <div class="operate-div" style="float:left">
                  <button v-if="controls[11]"  v-show="chargeFormParam.thisOptFlag>0" class="button-operate"   @click="remotePayClick" >
                    <i class="hasIcon hasImg">
                      <img src="dist/img/costControl/Long-range-charging-button.png" />
                    </i>
                    {{chargeFormParam.thisOptFlag == 3 && lastPayData.localControl == 1 ? "远程补写" : payTitle }}
                  </button>
                  <button v-if="controls[12]"
                          v-show=" (chargeFormParam.thisOptFlag==1 || chargeFormParam.thisOptFlag==4) && lastPayData.isAccount === 2 "
                          class="button-operate" @click="openDialog">
                    <i class="hasIcon hasImg">
                      <img src="dist/img/costControl/IC-card-icon.png" />
                    </i>
                    购电卡充值
                  </button>
                  <button
                    v-show="chargeFormParam.isAccount==2 && (chargeFormParam.thisOptFlag==3 || chargeFormParam.thisOptFlag==2) && lastPayData.isAccount === 2"
                    class="button-operate"
                  @click="openReWriteDialog">
                    <i class="hasIcon hasImg">
                      <img src="dist/img/costControl/IC-card-icon.png" />
                    </i>
                  {{chargeFormParam.thisOptFlag==3?"补写到购电卡":"取消远程充值并写入购电卡"}}
                  </button>
                  <button class="button-operate" v-if="lastPayData.localControl == 2 && parseFloat(lastPayData.remainAmount) > 0 && controls[30]" @click="switchOnOff">
                    <i class="hasIcon hasImg">
                      <img src="dist/img/costControl/Electricity-button.png"/>
                    </i>
                    远程合闸
                  </button>
                </div>
               </li>
             </ul>
           </div>
         </div>
        <div class="list-content detail-content list-contentB" v-if="(lastPayData.isAccount === 2 && lastPayData.localControl == 1) && lastPayData.isEnabled == 1">
          <div class="list-header">
            <i class="hasIcon hasImg" style="margin-top:-0.1em">
              <img src="dist/img/costControl/IC-card-icon.png" />
            </i>购电信息
            <!--controls[5]-->
            <button class="list-operate" @click="measureData" v-if="controls[5]">
              <i class="hasIcon">
                <icon name="podcast"></icon>
              </i>
               实时召测
            </button>
          </div>
          <ul class="form-list detail-list" >
            <li v-for="(item, $index) in pageViewData.payment">
              <span class="label-span" style="min-width:9.5em">{{item.name}}</span>
              <span  class="label-show hasLabel"  style="text-align: left;margin-left:0" :style="{color: item.color || 'auto'}" v-html="'： &nbsp;' + (item.formatFunc? formatFuncs[item.formatFunc](item.value,item): getValue(item.value))"></span>
            </li>
          </ul>
        </div>
        <div class="list-content detail-content list-contentB" v-if="(lastPayData.isAccount === 2 || lastPayData.localControl == 2) && lastPayData.isEnabled == 1 && lastPayData.payCount > 0">
          <div class="list-header">

            <i class="hasIcon hasImg" style="margin-top:-0.1em">
              <img src="dist/img/costControl/IC-card-icon.png" /></i>
            最后一次购电记录
            <button class="list-operate" @click="printPay" v-if="parseInt(lastPayData.payCount) > 0">
              <i class="hasIcon ">
                <!--<img src = 'dist/img/printBill.png'>-->
                <icon name="ticket"></icon>
              </i>
                打印小票
            </button>
          </div>
          <div class="showStatusImg">
            <template v-if="lastPayData.writeMeter ===1">
              <img src="dist/img/costControl/success.png"/>
              <p>成功</p>
            </template>
            <template v-else-if="lastPayData.writeMeter ===0 && lastPayData.payModel === 1">
              <img src="dist/img/costControl/Issueding.png"/>
              <p>远程下发</p>
            </template>
            <template v-else>
              <img src="dist/img/costControl/fail.png"/>
              <p>失败</p>
            </template>
          </div>
          <ul class="form-list detail-list" style="width: calc(92% - 3em); margin-top:1em">
            <li v-for="(item, $index) in pageViewData.lastPayment" v-if="item.isShow" style="width:calc(30% - 2em)">
              <span class="label-span " style="min-width:6.5em">{{item.name}}</span>
              <span class="label-show hasLabel"   style="text-align: left;margin-left:0" :style="{color: item.color || 'auto'}" v-html="'： &nbsp;' + (item.formatFunc? formatFuncs[item.formatFunc](item.value,item): getValue(item.value))"></span>
            </li>
          </ul>
        </div>
       </VuePerfectScrollbar>

      
      <loginAgain ref="loginAgain" @nextOperate="nextOperate" tipMsg="请输入当前用户密码以验证身份，回车提交数据" @closeOperate="closeOperate"></loginAgain>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="loading" ref="loadings"></dialogs>
      <dialogs type="confirm" ref="confirms"></dialogs>
      <dialogs type="confirm" ref="confirmes"></dialogs>
      <!--读写购电卡-->
      <dialogs type="customer" ref="readCard" width="48em" overflow="auto"  isPadding="true">
        <div slot="header">
            写购电卡
        </div>
        <div slot="content">
          <div class="operate-div" style="float:left;padding:0 0 0.8em 0; ">
            <span class="lbstyle">充值金额：</span>
            <span class="lbmoney"> ￥:{{viewMoney}}</span>
            <button class="button-operate" @click="writeCardClick">写卡</button>
            <button class="button-operate"  @click="readCardClick">读卡</button>
          </div>
          <table class="form-list table-list" style="width: 100%;margin-left:10px;width:calc(100% - 10px) ">
            <tr>
              <th style="width: 100px">名称</th>
              <th style="width: 120px">系统数据</th>
              <th style="width: 120px">读卡资料</th>
              <th style="width: 200px">备注</th>
            </tr>
            <tr v-for='item in cardTableRowData'>
              <td>{{item.name}}</td>
              <td>{{item.sysVal}}</td>
              <td>{{item.cardVal}}</td>
              <td class="tdremark">{{item.remark}}</td>
            </tr>
          </table>
        </div>
      </dialogs>
      <dialogs type="customer" ref="resultData" class="dialogTable" width="54em" maxHeight="580px" >
        <div slot="header">
          远程合闸结果
        </div>
        <div slot="content" class="tabContent" >
          <div class="tabs-show tab-div" style="margin:0 0 0.6em 0.7em !important;float:left">
            <ul>
              <li :class="{'selectedLi': tabIndex === $index}"  v-for="(myBtn, $index) in btnGroup" @click="getTab(myBtn, $index)" >
                {{myBtn.name}} ( {{myBtn.num}} )
              </li>
            </ul>
          </div>
          <button class="result-button" v-show="tabIndex === 1&& btnGroup[1].num>0 " @click="switchAgain" >
            <i class="hasIcon hasImg">
              <img src="dist/img/costControl/Electricity-button.png" />
            </i>
            再次执行
          </button>
            <div class="tabContent" style="height:340px;float: left;width: calc(100% - 1.4em);padding-top:0;margin-left:0.6em;font-size: 1.1em">
              <tables ref="resoluteTable" :tableOpt="resultOpt" @getTotals="getTotals" v-if="resultShow"></tables>
            </div>
        </div>
      </dialogs>
    </div>
  </div>
</template>

<script>
   
    import '../../../../dist/plugin/bgsocket/bg.jssocket.js'
    import flash from  '../../../component/childComponent/FlashPlugin.vue'
    import combine from  '../../../component/childComponent/CombineSelect.vue'
    import loginAgain from '../../childComponent/userValidate.vue'
    import {GUID} from'../../../assets/GUID.js'
    import 'vue-awesome/icons/fax'
    import 'vue-awesome/icons/podcast'
    import 'vue-awesome/icons/ticket'
    export default {
        components: {
            flash,
            combine,
            loginAgain
        },
        data() {
            return {
                btnGroup: [
                      {name: '操作成功', num: 0, url: 'onOff/queryResult', status: 1},
                      {name: '操作失败', num: 0, url: 'onOff/queryResult', status: 2}
                ],
                currentDeviceNo: '',
                payCount: '',
                lastMeterNo: "",//是否显示充值成功
                recall: '',
                controls: [],
                searchType: 'customerContact',
                showDialog: false,
               // tipsMesage: {msg: "", isReflsh: false, color: "red"},
                styleParams: '',
                //可输入的下拉框
                combineOption: {
                    position: 'right',
                    inputOpt: {
                        width: '100%',
                        type: 'typeSearch',
                        selectOpt: {
                            width: '7em',
                            cs: 'client',
                            data: [
                                {text: '联系方式', value: 'customerContact'},
                                {text: '用户地址', value: 'customerAddr'},
                                {text: '用户姓名', value: 'customerName'},
                                {text: '门牌编号', value: 'propertyName'}
                                // {text: '用户表号', value: 'deviceNo'}
                            ],
                            text: 'text',
                            value: 'value',
                            key: 'searchType'
                        }
                    },
                    url: 'pay/fuzzyQuery',
                    method: 'post',
                },
                //充值缴费窗口属性
                chargeFormParam: {
                    title: '充值缴费',
                    thisOptFlag: 1,       //当前操作 0 -未知 1-充值缴费 2-取消远程并补写IC卡信息 3-补写IC卡 4-充值信息未写入电表
                    isAccount: 2,         //当前操作 0 未开户 1-已开户制卡失败 2-已开户
                    reWriteMoney: 0      //重写金额
                },

                //支付信息
                payInfo: {
                    payMoney: 0,      //支付金额
                    payCount: 0,      //购电次数
                    payModel: 0,      //支付模式 1远程 2购电卡
                    customerNo: '',   //客户编号
                  //  cno: '',           //设备编号
                    payGuid: "购电单号",
                    payDate: "购电日期",
                  //  deviceNo: ''
                },
              
                //最后一次购电数据
                lastPayData: {
                 
                },
                //IC卡参数信息
                cardTableRowData: [{remark: '', name: '用户姓名', sysVal: '-', cardVal: '-', key: 'customerName'},
                    {remark: '', name: '联系电话', sysVal: '-', cardVal: '-', key: 'customerContact'},
                    {remark: '', name: '表计户号', sysVal: '-', cardVal: '-', key: 'meterUserNo'},
                    {remark: '', name: '用户地址', sysVal: '-', cardVal: '-', key: 'customerAddr'},
                    {remark: '', name: '用户表号', sysVal: '-', cardVal: '-', key: 'electMeterNo'},
                    {remark: '', name: '计费类型', sysVal: '-', cardVal: '-', key: 'dictItemName'},
                    {remark: '', name: '购电次数', sysVal: '-', cardVal: '-', key: 'payCount'},
                    {remark: '', name: '剩余金额', sysVal: '-', cardVal: '-', key: 'remainAmount'},
                    {remark: '', name: '透支金额', sysVal: '-', cardVal: '-', key: 'overdraftFee'},
                    {remark: '', name: '电流互感器变比', sysVal: '-', cardVal: '-', key: 'curTranfRto'},
                    {remark: '', name: '电压互感器变比', sysVal: '-', cardVal: '-', key: 'volTranfRto'},
                    {remark: '', name: '非法插卡次数', sysVal: '-', cardVal: '-', key: 'errorCnt'},
                    {remark: '', name: '密钥状态', sysVal: '-', cardVal: '-', key: 'keyState'},
                    {remark: '', name: '密钥条数', sysVal: '-', cardVal: '-', key: 'keyCount'},
                    {remark: '', name: '密钥版本', sysVal: '-', cardVal: '-', key: 'keyVer'},
                    {
                        remark: '',
                        name: '返写日期',
                        sysVal: '-',
                        cardVal: '-',
                        formater: "writeTimeFormater",
                        key: 'reWrtTime'
                    },

                ],
                //IC卡操作窗口
                cardFormParam: {
                    allowWrite: 1 //0不允许读写卡 1标识允许读卡 2标识允许写卡 3 读取的IC信息错误
                },
                tabIndex: 0,
                switchGuid: '',
                switchDate: '',
                resultShow: false,
                resultOpt: {
                  column: [],
                  pageSize: 20,
                  pageNumber: 1,
                  url: 'onOff/queryResult',
                  method: 'post',
                  height: 260,
                  noFooter:true
               },
            }
        },
        methods: {
            //召测结果tab切换页
           getTab: function (btn, index) {
             this.tabIndex = index;
             let that = this;
             that.$refs.resoluteTable.resetTableOpt({
                queryParam: {
                  guid: that.switchGuid,
                  dataFlag: that.btnGroup[that.tabIndex].status,
                  deviceType: '07',
                  date: that.switchDate
                },
                column: that.columns[index]
              });
            },
            switchAgain: function () {
                let tables = this.$refs.resoluteTable;
                tables.getAllSelect();
                let selectAgain = tables.getSelected();
                let lenAgain = selectAgain.length;
                let arr = [];
                for(let i=0; i<lenAgain; i++) {
                  arr.push({customerNo: selectAgain[i].customerNo, deviceCno: selectAgain[i].cno, jzqCno: selectAgain[i].jzqCno})
                }
                this.closeResult();
                this.operateSwitch(arr);
            },
            switchOnOff: function () {
              let that = this;
              that.$refs.confirms.show({
                msg: '确定要进行合闸操作嘛？',
                action: {
                  confirm: function () {
                    that.operateSwitch();
                  }
                }
              })
            },
            operateSwitch: function (arr) {
              this.switchGuid = new GUID().newGUID();
              let param = {
                  guid: this.switchGuid,
                  onOff: 1,
                  reason: '充值通电',
                  deviceType: '07',
              };
              param['meters'] = arr ? arr : this.lastPayData.meters;
              let that = this;
              that.$refs.loadings.show({
                msg: '正在合闸，请稍后。。。',
                hasClose: true,
                action: {
                  cancel: function () {
                    that.getRequest({
                      url: 'onOff/stopCollectList',
                      method: 'post',
                      param: {guid: that.switchGuid},
                      success: function () {
                        that.closeSwitchO();
                      },
                      error: function (data) {
                        that.alerts.show({msg: data.message});
                        that.closeSwitchO();
                      }
                    }, true);
                  }
                }
              });
              that.getRequest({
                url: 'onOff/onOff',
                param: param,
                method: 'post',
                success: function (data) {
                  that.switchDate = data.data;
                  that.getBackRespon(that.switchGuid)
                },
                error: function (data) {
                  that.alerts.show({msg: data.message});
                  that.$refs.loadings.close();
                }
              });
            },
            getBackRespon: function (guid) {
               let that = this;
      
               that.getWebsocket({
                keyMsg: 'onOff',
                keyStatus: 'onOffStatus',
                paramStatus: {
                  guid: guid,
                  dataFlag: 'onOffStatus'
                },
                sign: true,
                funMsg: function (data) {
                  let datas= data.data;
                  
                  if (datas.taskStatus != 101) {
                    that.closeSwitchO();
                  }
                },
                funStatus: function (data) {
                  that.closeSwitchO();
                  
                 }
              })
            },
            closeSwitchO: function () {
              // this.reflshPayInfo();
               this.$refs.loadings.close(0);
               this.$store.state.callback['onOff'] = null;
               this.$store.state.callback['onOffStatus'] = null;
               this.resoluteTableShow();
            },
            closeResult: function () {
              this.$refs.resultData.close();
              this.resultShow = false;
              this.tabIndex = 0;
              this.btnGroup[0].num =  this.btnGroup[1].num = 0;
            }, 
            getTotals: function (val) {
                this.btnGroup[this.tabIndex].num = val;
            },
            resoluteTableShow: function () {
               this.resultOpt.url =  'onOff/queryResult';
               this.resultOpt.queryParam = {
                  guid: this.switchGuid,
                  deviceType: '07',
                  dataFlag: 1,
                  date: this.switchDate
               };
               this.resultOpt.column = this.columns[0];
               this.resultShow = true;
               this.$refs.resultData.show({
                  hasClose: true,
                  action: {
                   cancel: function () {
                        that.closeResult()
                  }
                }
               });
               let p = {guid: this.switchGuid, dataFlag: this.btnGroup[1].status, deviceType: '07', date :  this.switchDate, pageNumber: 1, pageSize: 20};
               let that = this;
               that.getRequest({
                    url: 'onOff/queryResult',
                    method: 'post',
                    param: p,
                    success: function (data) {
                        that.btnGroup[1].num = data.total;
                     },
                    error: function () {
                       that.btnGroup[1].num = 0;
                     }
                })
           },
           //查询最后一次购电信息
          //选框的回调函数
            searchLastPay: function (data) {
               if(data !== '') {
                  let sParam = {};
                  sParam["customerNo"] = data.value;
                  // this.currentDeviceNo = arr[1];
                  this.queryCustomer({sParam: sParam});//提交查询客户档案信息
              }   
            },
          //查询客户最后一次购电信息
          /*
           param={ sParam:"查询数据对象，例如：{customerNo:xxxx}",
           cardData:"读取数据"
           }
           * */
           queryCustomer: function (param, flag) {
               let that = this;
               let sParam = param.sParam;
               that.lastPayData = that.cleanSearchData(that.lastPayData);

               //清空写卡数据
               this.initWindowObj();
               //请求详细信息
               this.getRequest({
                 url: 'pay/queryUser',
                 method: 'post',
                 param: sParam,
                 success: function (data) {
                   let info = data.data;
                   that.lastPayData = info;//保存当前最后一次购电信息
                   that.chargeFormParam.isAccount = info.isAccount || 0; //开卡状态
                   that.payInfo.customerNo = info.customerNo; //客户唯一标识信息
                  // that.payInfo.deviceNo = info.electMeterNo || '';
                  // that.payInfo.cno =info.cno || '';               //电表唯一标识
                   that.payInfo.payCount = info.payCount;  //电表充值次数
                   that.$set(that.lastPayData, 'localControl', 2);  
                   that.$set(that.lastPayData, 'isEnabled', 1);    
                   that.getTipMsg(); //消息提示
                  //用户需要开户后进行后面的动作
                    if (info.isAccount === 2 && that.lastPayData.isEnabled === 1) {
                  //判断上次的购电信息是否成功写入电表 0-正在远程 2-取消远程 3-表示充值失败
                      if (info.writeMeter !== 1) {
                        if (info.writeMeter === 0) {
                         //判断充值信息是否已经写入IC卡
                          if (param.cardData) {
                            //比较当前的购电信息是否和IC卡的购电信息一致
                            that.compareCard({
                              data: param.cardData,
                              //验证通过的回调函数
                              passCallBack: function () {
                                that.chargeFormParam.reWriteMoney = 0;
                                that.payInfo.payMoney = 0;
                              },
                             //比较完成的回调函数
                              callback: function () {
                                //检查没有写入IC卡后的回调函数
                                that.chargeFormParam.thisOptFlag = info.payModel === 1 ? 2 : 3;//补写卡
                              }
                            });
                          }
                          else {
                            that.chargeFormParam.thisOptFlag = info.payModel === 1 ? 2 : 3;//补写卡
                          }
                        }
                        else {
                          //thisOptFlag 2-取消并补写 3-补写
                          that.chargeFormParam.thisOptFlag = info.writeMeter === 0 ? 2 : 3; //只能补写IC卡信息
                        }
                        that.chargeFormParam.reWriteMoney = info.payMoney; //补写金额信息
                        that.payInfo.payMoney = info.payMoney; //显示补写金额信息
                     }
                     else {
                       //读卡器读出来的数据，标识充值数据未写入电表的时候，暂时不充值
                       that.chargeFormParam.thisOptFlag = that.chargeFormParam.thisOptFlag === 4 ? 4 : 1; //充值缴费
                       that.payInfo.payMoney = 0;
                     }
                   }
                   if(flag) {
                       that.alerts.show({msg: "刷新成功！"});
                   }
                 },
                 error: function () {
                   that.alerts.show({msg: "用户信息在系统未找到，请核实输入信息！"});
                 }

             });
           },
           queryCustomerInfo: function (param) {
                let that = this;
                this.getRequest({
                    url: 'pay/queryUser',
                    method: 'post',
                    param: param,
