                    });//提交查询客户档案信息

                    if (/^0+$/gi.test(reCardData.data.electMeterNo)) {
                        this.alerts.show({msg: '当前购电卡中有购电金额需要充值到电表，请先在电表上充值后再缴费！'});
                        this.chargeFormParam.thisOptFlag = 4; // 4-充值信息未写入电表
                    }

                }
                else {
                    this.alerts.show({msg: '读卡信息有误！'});
                }
          },
          getTabColumn: function (key, dataObj) {
                let rowItem = null;
                dataObj.forEach(function (item) {
                    if (item.key === key) {
                        rowItem = item;
                        return true;
                    }
                });
                return rowItem;
          },



            //比较最后一次购电记录和电卡是否一致(补写卡的时候需要做)
            /*
             obj={data:"读卡数据的对象 callback:"回调函数"}
             */
          compareCard: function (obj) {
                let data = obj.data;
                let callback = obj.callback;      //验证未通过的回调函数
                let passCallBack = obj.passCallBack; //验证通过的回调函数
                let that = this;
                if (this.padLeft(this.lastPayData.meterUserNo, 12) == data.lastCustomerNo &&
                    this.padLeft(this.lastPayData.electMeterNo, 12) == data.lastDeviceNo &&
                    this.lastPayData.payCount == data.lastPayCount &&
                    parseFloat(this.lastPayData.payMoney) == data.lastPayMoney) {

                    if (this.lastPayData.payModel === 2 && this.lastPayData.writeMeter === 0) {
                        //更新数据库的状态为已写卡
                        this.updateCardPay({
                            writeMeter: 1, //更新状
                            payModel: 2,    //购电卡充值
                            callback: function () {
                                that.lastPayData.writeMeter = 1;
                                that.chargeFormParam.thisOptFlag = 1;

                                that.alerts.show({msg: "购电记录已经写入购电卡中，请到电表中充值。"});
                                that.closeDialog();//关闭写卡窗口

                                that.getTipMsg();

                                //验证成功的回调函数
                                if (passCallBack && typeof passCallBack === 'function') {
                                    passCallBack();
                                }
                            }
                        });
                    }
                    else {
                        callback();//执行回调函数
                    }
                }
                else {
                    callback();//执行回调函数
                }
          },
          remotePayClick: function () {
               let that = this;
                    if (this.submitChk()) {
                        if (this.chargeFormParam.thisOptFlag === 1) {
                            if (this.submitChk()) {
                                this.payInfo.payModel = 1;//远程
                                this.$refs.confirms.show({
		                              msg: "确定对 <span style='color:#EE8836;font-size: 1.1em' >" + this.lastPayData.customerName + "</span> 充值 <span style='color:#EE8836;font-size: 1.1em' >￥" + this.payInfo.payMoney + "</span> ?",
                                  action: {
                                    confirm: function () {
                                      that.remotePay();
                                    }
                                  }
                                });
                            }
                        }
                        else {
                            this.$refs.confirms.show({
                              msg: "确定对当前购电信息远程下发电表?",
                              action: {
                                confirm: function () {
                                  //更新数据库的状态为已写卡
                                  that.updateCardPay({
                                    writeMeter: 0, //更新状
                                    payModel: 1,   //远程充值
                                    callback: function () {
                                      //that.alerts.centerDialog({msg: "开启远程对电表充值缴费，操作成功!"});
                                      //that.closeDialog();//关闭写卡窗口

                                      //刷新最后一次购电信息
                                      //that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo}});

                                      that.reflshRemotePay();
                                    }
                                  });
                                }
                              }
                            })
                        }
                    }

          },
            //远程充值保存
          remotePay: function () {
                this.payInfo.payModel = 1;//远程
                let payCount = this.payInfo.payCount;
                if (this.chargeFormParam.thisOptFlag === 1) {
                    payCount = this.payInfo.payCount + 1;
                }
                this.submitPay(payCount); //提交支付
          },
            //本地购电卡充值
          localPay: function () {
                this.payInfo.payModel = 2; //本地购电卡
                let payCount = this.payInfo.payCount;
                if (this.chargeFormParam.thisOptFlag === 1) {
                    payCount = this.payInfo.payCount + 1;
                }
                this.submitPay(payCount);
          },
            //提交充值保存
          submitPay: function (payCount) {
               let that = this;
               that.payCount = payCount;
               that.$refs.loginAgain.show();
          },
          submitPayData: function () {
                let that = this;
                let payCount = this.payCount;
                this.loadings.show({
                    msg: '正在提交充值,请稍后...'
                });
               
                let submitParam = this.extendObj({}, this.payInfo);
                submitParam.payCount = payCount;//充值次数
               // submitParam.meterUserNo = this.lastPayData.meterUserNo;
                //提交支付信息
                this.getRequest({
                    url: 'pay/remoteRecharge',
                    method: 'post',
                    param: submitParam,
                    success: function (data) {
                        let Data = data.data;
                    //    let optName = (that.payModel === 1 ? "远程" : "购电卡");
                      that.lastPayData.payDate = Data.createDate;
                      that.lastPayData.payGuid = Data.payGuid;

                      if (data.flag === 401) {
                        that.loadings.close();
                        that.$refs.loginAgain.show();
                      } else {
                           if(that.lastPayData.localControl == 2) {
                               that.loadings.close();
                               that.alerts.show({msg: data.message});
                               that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo}});//提交查询客户最后一次购电信息
                           } else {
                              if (that.payInfo.payModel === 2) {
                               that.loadings.close();
                               //写IC卡
                                that.writeCard(payCount);
                              //刷新最后一次购电信息
                               that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo, deviceNo: that.payInfo.deviceNo}});//提交查询客户最后一次购电信息
                            }
                            else {
                              that.reflshRemotePay();
                           }
                        }
                      }
                    },
                  error: function (data) {
                    that.loadings.close();
                    that.closeDialog();
                    that.alerts.show({msg: data.message});
                    that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo, deviceNo: that.payInfo.deviceNo}});//提交查询客户最后一次购电信息
                  }
                });
            },
            reflshRemotePay: function () {
                let that = this;
                that.loadings.show({
                    msg: '正在远程充值电表,请稍后...'
                });
                that.reCall('pay/readStatus', {payGuid: that.lastPayData.payGuid, type: 1}, {
                    necessary: function (data) {
                        clearInterval(that.recall);
                        that.loadings.close();
                        that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo, deviceNo: that.payInfo.deviceNo}});//提交查询客户最后一次购电信息
                    },
                    error: function (data) {
                        that.$refs.confirmes.show({
                          msg: '远程充值失败，已改为自动下发！是否取消远程改写购电卡充值？',
                          action: {
                            confirm: function () {
                              that.openReWriteDialog();
                            },
                            cancel: function () {
                              //打印购电数据
                              that.$refs.confirms.show({
                                msg: '是否打印缴费小票？',
                                action : {
                                  confirm: function () {
                                    that.printPay()
                                  }
                                }
                              })
                            }
                           }
                        });

                    },
                    success: function (data) {
                      that.$refs.confirms.show({
                        msg: '远程充值成功，并下发到电表,是否打印缴费小票？',
                        action : {
                          confirm: function () {
                            that.printPay()
                          }
                        }
                      })
                    },
                    exception: function (data) {
                        that.alerts.show({msg: data.message});
                    }
                   }
                );
            },
            reCall: function (url, param, callback) {
                let sign = true;
                let that = this;
                that.recall = setInterval(function () {
                    if (sign) {
                        sign = false;
                        that.getRequest({
                            url: url,
                            param: param,
                            method: 'post',
                            success: function (data) {
                                //status
                              switch (data.data) {
                                case 101:
                                  sign = true;
                                  break;
                                case 1: //充值成功
                                  callback.necessary(data);
                                  if (callback.success) {
                                    callback.success(data);
                                  }
                                  break;
                                default://失败
                                  callback.necessary(data);
                                  if (callback.error) {
                                    callback.error(data);
                                  }
                                  break;
                              }
                            },
                            error: function (data) {
                                that.loadings.close();
                                callback.necessary(data);
                                if (callback.exception) {
                                  callback.exception(data);
                                }
                            }
                        }, true)
                    }
                }, 1000);
            },
            //打开dialog窗口
            openDialog: function () {
             //   if(this.controls[12]){
                    if (this.submitChk()) {
                    let that = this;
                    that.showDialog = true;
                    setTimeout(function () {
                       that.$refs.readCard.show({
                         hasClose: true,
                         action: {
                             cancel: function () {
                                 that.closeDialog();
                             }
                         }
                       });
                    }, 0);
                    //清空数据
                    this.initClearData(this.cardTableRowData, [{key: 'sysVal', value: '-'}, {
                        key: 'cardVal',
                        value: '-'
                    }, {key: 'remark', value: '-'}]);

                    //打开立即读取系统的用户信息
                     this.queryCstInfo();
                  }
            //    }
//                else{
//                    this.alerts.show({msg: '暂无本地购电充值权限！'});
//                }
            },
            //打开补写IC卡窗口
            openReWriteDialog: function () {
                let that = this;
                this.payInfo.payMoney = this.chargeFormParam.reWriteMoney; //
                //先取消远程充值
                this.cancelCharge(function () {
                    that.openDialog(); //打开窗口
                });

            },
            //关闭dialog窗口
            closeDialog: function () {
                this.$refs.readCard.close();
                this.showDialog = false;
            },
            //读IC卡信息
            readCardClick: function () {
                if (this.cardFormParam.allowWrite > 0) {
                    //清空数据
                    this.initClearData(this.cardTableRowData, [{key: 'cardVal', value: '-'}]);
                    this.loadings.show({
                        msg: '正在读卡,请稍后...',
                        action: {
                          cancel: function () {
                            disconnect();//关闭连接
                          }
                        }
                    });
                    //开始读卡
                    bgFlashReadCard({
                        success: this.readSuccess,
                        callback: this.bgFlshCallback
                    });
                }
                else {
                    this.alerts.show({msg: '当前不能读取IC卡信息'});
                }
            },
            //格式化返写时间
            writeTimeFormater: function (val) {
                return '20' + val.substring(0, 2) + '-'
                    + val.substring(2, 4) + '-'
                    + val.substring(4, 6) + ' '
                    + val.substring(6, 8) + ':'
                    + val.substring(8, 10);
            },
            //读卡之后的成功回调信息
            readSuccess: function (data) {
                let that = this;
                let allowWrite = 2; //读卡成功允许写卡
                let reCardData = JSON.parse(data);
                let isSaveWriteInfo = true; //是否保存读卡记录

                //加载读卡信息
                let loadCardView = function () {
                   
                    if (/^0+$/gi.test(reCardData.data.lastDeviceNo) && reCardData.data.lastDeviceNo == reCardData.data.electMeterNo) {
                        //当前为新卡
                        for (let key in reCardData.data) {
                            let rowItem = that.getTabColumn(key, that.cardTableRowData);
                            if (rowItem !== null) {
                                rowItem.remark = "新卡";
                            }
                        }
                    }
                    else {
                        for (let key in reCardData.data) {
                            let rowItem = that.getTabColumn(key, that.cardTableRowData);
                            if (rowItem !== null) {
                                rowItem.cardVal = reCardData.data[key];
                                rowItem.remark = "--";

                                //判断充值次数,是否异常
                                if (key === 'payCount') {
                                    if (rowItem.cardVal === 0 || rowItem.cardVal !== rowItem.sysVal) {
                                        allowWrite = 3;
                                        if (rowItem.sysVal > rowItem.cardVal) {
                                            rowItem.remark = "请先将购电卡在电表上插一次";
                                        }
                                        else {
                                            rowItem.remark = "购电次数异常";
                                        }
                                    }
                                    if (rowItem.cardVal === 0) {
                                        isSaveWriteInfo = false;
                                    }
                                }
                                //判断表计户号是否一致
                                if (key === 'meterUserNo') {
                                    if (rowItem.cardVal !== rowItem.sysVal) {
                                        allowWrite = 3;
                                        rowItem.remark = "表计户号不一致";
                                    }
                                }
                                //判断表号是否一致
                                if (key === 'electMeterNo') {
                                    if (rowItem.cardVal !== rowItem.sysVal) {
                                        allowWrite = 3;
                                        rowItem.remark = "用户表号不一致";
                                    }
                                }
                                //格式化数据
                                if (rowItem.formater) {
                                    rowItem.cardVal = that[rowItem.formater](reCardData.data[key]);
                                }
                            }
                        }
                     that.queryCustomerInfo({deviceNo: reCardData.data.lastDeviceNo});
                    }
                    that.cardFormParam.allowWrite = allowWrite; //允许写卡allowWrite

                    //保存读卡信息
                    if (isSaveWriteInfo) {
                        that.saveWrieteCard(reCardData.data);
                    }

                };
                // alert(that.cardFormParam.allowWrite);
                //首先判断数据是否已经写入IC卡
                if (that.chargeFormParam.thisOptFlag === 3) //补卡
                {
                    //比较是否已经将充值信息写入了IC卡
                    this.compareCard({
                        data: reCardData.data, //读卡数据的对象
                        callback: loadCardView  //回调函数
                    });
                }
                else {
                    loadCardView();
                }
            },
            //写IC卡事件
            writeCardClick: function () {
                let that = this;
                if (this.writeCardChk()) {
                    this.payInfo.payModel = 2;//本地购电卡
                    if (this.chargeFormParam.thisOptFlag === 1) {
                      that.$refs.confirms.show({
	              msg: "确定对 <span style='color:#EE8836;font-size: 1em' >" + this.lastPayData.customerName + "</span> 购电卡中充值 <span style='color:#EE8836;font-size: 1em' >￥:" + this.payInfo.payMoney + "</span> ?",
                        action: {
                          confirm: function () {
                            that.localPay();
                          }
                        }
                      });
                    }
                    else {
                        //直接写IC卡
                        this.writeCard(this.payInfo.payCount);
                    }
                }
            },
            //写IC卡操作
            writeCard: function (payCount) {
                if (this.cardFormParam.allowWrite === 2) {
                    this.loadings.show({
                        msg: '正在写卡,请稍后...'
                    });
                    let writeData = {
                        paramUpdate: 0,
                        electMeterNo: this.payInfo.cno.substring(this.payInfo.cno.length - 12, this.payInfo.cno.length),
                        customerID: this.padLeft(this.lastPayData.meterUserNo, 12),
                        payMoney: parseFloat(this.payInfo.payMoney),
                        payCount: payCount,//this.payInfo.payCount,
                        cardType: 2 //购电卡
                    };

                    //写IC卡信息
                    bgFlashWriteCard({
                        success: this.writeSuccess,
                        jsonObj: writeData,
                        meterType: this.lastPayData.meterType,//写表类型
                        callback: this.bgFlshCallback
                    });
                }
                else {
                    this.alerts.show({msg: 'IC卡信息有误不能写IC卡'});
                }
            },
            //写IC卡成功函数
            writeSuccess: function (data) {
                let that = this;

                this.cardFormParam.allowWrite = 1; //不允许写
                this.updateCardPay({
                    writeMeter: 1, //写卡成功
                    payModel: 2,  //购电卡充值
                    callback: function () {
                        //刷新最后一次购电信息
                        that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo, deviceNo: that.payInfo.deviceNo}});//提交查询客户最后一次购电信息
                        that.closeDialog();

                        //打印购电数据
                        that.$refs.confirms.show({
                          msg: "是否打印缴费小票？",
                          action: {
                            confirm: function () {
                              that.printPay()
                            }
                          }
                        });
                    }
                }); //更新电卡充值结果
            },
            //flash的成功、失败回调函数
            bgFlshCallback: function (msg) {
                this.loadings.close();
                if (typeof  msg !== 'undefined' && msg !== '') {
                    this.alerts.show({msg: msg});
                }
            },
            //打印购电数据
            printPay: function () {
                let that=this;
                this.loadings.show({
                    msg: '正在打印小票,请稍后...',
                    action: {
                      cancel: function () {
                        disconnect();//关闭连接
                      }
                    }
                });
                this.print({
                    title: "充值凭证",
                    printContent: this.printVal,
                    callback: this.bgFlshCallback,
                    success:function(){
                        that.loadings.close();
                    }
                });
            },
            measureData: function () {
                let that = this;
                that.loadings.show({
                    title: '召测提示',
                    msg: '正在召测' + that.lastPayData.customerName + '的电表金额，请稍后。。。',
                    hasClose: true
                });
                that.getRequest({
                    url: 'pay/sendReadAmountAndCntCmd',
                    method: 'post',
                    param: {meterUserNo: that.lastPayData.meterUserNo, deviceCno:  that.payInfo.cno},
                    success: function (data) {
                      that.reCall('pay/readStatus', {payGuid: data.data, type: 2}, {
                        necessary: function (data) {
                          clearInterval(that.recall);
                          that.loadings.close();
                        },
                        error: function (data) {
                          that.alerts.show({msg: '召测失败'});
                        },
                        success: function (data) {
                          that.alerts.show({msg: data.message});
                          that.queryCustomer({sParam: {customerNo: that.payInfo.customerNo, deviceNo: that.payInfo.deviceNo}});//提交查询客户最后一次购电信息
                        },
                        exception: function (data) {
                          that.alerts.show({msg: data.message});
                        }
                      } );
                    },
                    error: function (data) {
                       that.loadings.close();
                       that.alerts.show({
                        msg: data.message
                      })
                    }
                }, true)
            },
            //查询客户IC开户档案资料
            queryCstInfo: function () {
                let that = this;
                //请求详细信息
                this.getRequest({
                    url: 'pay/queryUserPay',
                    method: 'post',
                    param: {
                        customerNo: this.payInfo.customerNo,
                        deviceNo: this.payInfo.deviceNo
                    },
                    success: function (data) {
                        //payResultInfo
                      let info = data.data;
                      for (let key in info) {
                        let rowItem = that.getTabColumn(key, that.cardTableRowData);
                        if (rowItem !== null) {
                          if (key === 'payCount' && that.chargeFormParam.thisOptFlag === 3) {
                            rowItem.sysVal = info[key] - 1; //补写IC卡，系统充值次数多一次
                          }
                          else {
                            rowItem.sysVal = info[key];
                          }
                        }
                      }
               
                      //that.cardFormParam.allowWrite = 1; //允许读写
                      //自动读卡
                      that.readCardClick();
                    },
                  error: function (data) {
                    that.alerts.show({msg: data.message});
                  }
                }, true);
            },
            //取消远程充值
            cancelCharge: function (callback) {
                if (this.chargeFormParam.thisOptFlag === 2) {
                    let that = this;
                    //请求详细信息
                    this.getRequest({
                        url: 'pay/updateWriteMeter',
                        method: 'post',
                        param: {
                            payGuid: that.lastPayData.payGuid,
                            payModel: 1,  //取消远程充值，采用购电卡充值
                            writeMeter: 2
                        },
                        success: function () {
                          that.chargeFormParam.thisOptFlag = 3; //补写IC卡
                          that.lastPayData.writeMeter = 2;
                          callback(); //执行取消之后的回调函数
                        },
                      error: function (data) {
                        that.alerts.show({msg: data.message});
                      }
                    }, true);
                }
                else {
                    callback();//执行取消之后的回调函数
                }
            },
            //保存读卡资料信息
            saveWrieteCard: function (paramData) {
                let that = this;
                //读取的返写卡信息，为00000 标识电卡数据未写入电表
                if (!/^0+$/gi.test(paramData.electMeterNo)) {
                    paramData["customerNo"] = this.payInfo.customerNo;
                    paramData["meterNo"] = paramData.electMeterNo;
                    paramData["reWrtTime"] = this.writeTimeFormater(paramData.reWrtTime);
                    paramData["cno"] = this.payInfo.cno;
                    paramData["meterUserNo"] = paramData["customerID"];
                    //请求详细信息
                    this.getRequest({
                        url: 'pay/saveCardRecord',
                        method: 'post',
                        param: paramData,
                        success: function (data) {
                          //that.alerts.show({msg: data.message});
                        },
                      error: function (data) {
                         let msg = data.message || '读卡信息保存失败!';
                         that.alerts.show({msg: msg});
                      }
                    });
                }
            },
            //更新写卡情况
            /*
             param= {writeMeter:1, //更新状
             payModel:"充值方式",
             callback:"回调函数"}
             * */
            updateCardPay: function (param) {
                let callback = param.callback;//回调函数
                let that = this;
                //请求详细信息
                this.getRequest({
                    url: 'pay/updateWriteMeter',
                    method: 'post',
                    param: {
                        payGuid: that.lastPayData.payGuid,
                        payModel: param.payModel,
                        writeMeter: param.writeMeter
                    },
                    success: function (data) {
                        that.loadings.close();
                        that.cardFormParam.allowWrite = 1;//允许读卡
                      //回调函数
                        if (typeof callback === 'function') {
                          callback();
                        }
                        else {
                          let optName = (that.payModel === 1 ? "远程" : "购电卡");
                          that.alerts.show({msg: optName + "" + 缴费成功});
                        }

                    },
                   error: function (data) {
                     that.loadings.close();
                   //  that.cardFormParam.allowWrite = 1;//允许读卡
                     that.alerts.show({msg: data.message});
                   }
                }, true);
            },
            //清空默认的数据初始化
            initClearData: function (objData, keyVals) {
                objData.forEach(function (item) {
                    for (let i = 0; i < keyVals.length; i++) {
                        let keyval = keyVals[i];//键
                        item[keyval.key] = keyval.value; //清空默认值
                    }
                });
            },
            //初始化当前窗口的对象值
            initWindowObj: function () {
                this.lastPayData = {};//清空
                this.tipsMesage = {msg: "", isReflsh: false, color: "red"};


                this.chargeFormParam = {
                    thisOptFlag: 1,       //当前操作 0 -未知 1-充值缴费 2-取消远程并补写IC卡信息 3-补写IC卡
                    isAccount: 0,         //当前操作 0 未开户 1-已开户制卡失败 2-已开户
                    reWriteMoney: 0,      //重写金额
                    meterType: 2           //0 其他 1- 09 2- 13
                };

                this.payInfo = {
                    payMoney: 0,      //支付金额
                    payCount: 0,      //购电次数
                    payModel: 0,      //支付模式 1远程 2购电卡
                    customerNo: '',   //客户编号
                   // cno: '',           //设备编号
                   // deviceNo: ''
                };

            },

            //消息提示
            submitChk: function () {
                if (!this.lastPayData.customerNo) {
                    this.alerts.show({msg: '请查询需充值缴费的用户'});
                    return false;
                }
                //用户是否开户
                if (this.chargeFormParam.isAccount !== 2 && this.lastPayData.localControl == 1) {
                    this.alerts.show({msg: "用户尚未开户,请先开户后再充值缴费"});
                    return false;
                }
                if (this.lastPayData.isEnabled !== 1) {
                    this.alerts.show({msg: "用户已经销户不能充值缴费！"});
                    return false;
                }
                if (this.chargeFormParam.thisOptFlag === 4) {
                    this.alerts.show({msg: "购电卡中购电信息，还未充值到电表中，请充值到电表后再购电"});
                    return false;
                }
                if (!this.regularTestForVal({val: this.payInfo.payMoney, regex: 'float', msg: "充值金额"})) {
                    return false;
                }
                let inputVal = parseFloat(this.payInfo.payMoney);
                if (inputVal <= 0 || inputVal > 10000) {
                    this.alerts.show({msg: "充值缴费金额不能少于￥0元，也不能超过￥10000.00元"});
                    return false;
                }
                return true;

            },
            //写卡检查
            writeCardChk: function () {
                switch (this.cardFormParam.allowWrite) {
                    case 2:
                        return true;
                    case 3:
                        this.alerts.show({msg: '购电卡信息和系统不匹配，请将购电卡在电表上插卡一次'});
                        return false;
                    default:
                        this.alerts.show({msg: '请先读取购电卡里信息'});
                        return false;
                }
            },
            //金额输入框的判断
            keyUp: function (e) {
                let str = this.payInfo.payMoney;
                let strChar = e.key;
                if (!/[0-9]|[.]/.test(strChar)) {
                    let re = new RegExp(strChar, "gi");
                    str = this.payInfo.payMoney.toString().replace(re, '');
                }
                if (this.payInfo.payMoney.length > 9) {
                  str = this.payInfo.payMoney.toString().substring(0, 9);
                }
                let arr = str.toString().split('.');

                if(arr.length > 1 && arr[1].length > 2) {
                   arr[1] =  arr[1].substring(0,2);
                   this.payInfo.payMoney = arr.join('.');
                } else {
                   this.payInfo.payMoney = this.payInfo.payMoney;
                }
            },
            //输入值校验
            inputFocus: function () {
                let inputVal = '';
                let str = this.payInfo.payMoney;
                if(str != '') {
                     if(str.toString().indexOf('.') > -1) {
                       inputVal = parseFloat(this.payInfo.payMoney).toFixed(2);
                     } else {
                      inputVal = parseInt(this.payInfo.payMoney);
                   }
                } else {
                  inputVal = 0;
                }
                this.payInfo.payMoney = inputVal;
            },
            init: function () {

            }
        },
        mounted: function () {
            let that = this;
            that.isIn();
            that.getAction();
        },
        computed: {
             columns: function  () {
           return [
             [
              {
                title: '状态',
                field: 'isOn',
                textAlign: 'center',
                width: '100',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  let operate;
                  let that = obj.$parent.$parent;
                  if (val === 1) {
                    operate = '通电'
                  } else {
                    operate = '断电' 
                  }
                  return '<span >' + operate + '</span>'
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', sortable: false, width: '100'},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: 180},
              {title: '联系电话', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120', sortable: false},
              {
                title: '用户表号',
                field: 'cno',
                textAlign: 'center',
                sortable: false,
                width: '120',
                type: 'string',
                formatter: function (val) {
                  if (val !== '' && val !== null) {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
              {title: '操作人', field: 'lastOnOffUserName', textAlign: 'center', width: '100', type: 'string', formatter: function (val) {return val || '--'}},
              {
                title: '操作时间',
                field: 'lastOnOffTime',
                textAlign: 'center',
                width: '130'
              }
            ],
            [
              {
                title: '状态',
                field: 'isOn',
                textAlign: 'center',
                width: '100',
                sortable: false,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  let operate;
                  let that = obj.$parent.$parent;
                  if (val === 1) {
                    operate = '通电'
                  } else {
                    operate = '断电' 
                  }
                  return '<span >' + operate + '</span>'
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', sortable: false, width: '100'},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: 180},
              {title: '联系电话', field: 'customerContact', textAlign: 'center', sortable: false, width: '120'},
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120', sortable: false},
              {
                title: '用户表号',
                field: 'cno',
                textAlign: 'center',
                sortable: false,
                width: '120',
                type: 'string',
                formatter: function (val) {
                  if (val !== '' && val !== null) {
                    return val.substr(2).replace(/^0*/gi, '')
                  }
                }
              },
              {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '260', sortable: false},
              {
                title: '操作时间',
                field: 'lastOnOffTime',
                textAlign: 'center',
                width: '130'
              }
            ]
           ]
        },
           payTitle: function () {
              if (this.lastPayData.localControl == 2) {
                 return '充值';
               }
              else {
                 return '远程充值';
               }
            },
            operateFlag: function () {
               //return this.lastPayData.isEnabled == 1 && ((this.lastPayData.localControl == 1 && this.lastPayData.isAccount === 2) || this.lastPayData.localControl == 2 )
               return true
            },
            viewMoney: function () {
                return parseFloat(this.payInfo.payMoney).toFixed(2);
            },
            alerts: function () {
                return this.$refs.alerts
            },
            loadings: function () {
                return this.$refs.loadings
            },
            //格式化数据
            formatFuncs: function () {
                let that = this;
                return {
                    'formatState': function (val) {

                        if (that.lastPayData.isAccount === 2) {
                            if (that.lastPayData.payModel === 2 && val === 0) {
                                return '<span style="color:red;">写卡失败</span>';
                            }
                            else {
                               
                                let item = that.getEnumItem('chargeState', val);
                                if (item.obj !== null) {
                                    return item.obj.html;
                                }
                                else {
                                    return item.text;
                                }
                            }
                        }
                    },
                    'formatModel': function (val) {


                        return that.getEnumItem('chargModel', val).text;

                    },
                    'formatMeterType': function (val) {
                        if(typeof val == 'undefined') {
                          return '--'
                        } else {
                          switch (val) {
                            default:
                                return '其他';
                            case 1:
                                return '09表';
                            case 2:
                                return '13表';
                          }
                        }
                      },
                     'formatIsOn' : function (val) {
                        if (typeof val == 'undefined') {
                           return '--'
                       } else {
                           switch (val) {
                            case 1:
                               return '<span style="color: #68C491;font-size:1em" >通电</span>';
                            case 0:
                               return '<span style="color: #F54B4C;font-size:1em" >断电</span>';
                         }
                       }
                    },
                    'formatAccount': function (val) {
                        if(typeof val == 'undefined') {
                          return '--'
                        } else {
                          if (typeof that.lastPayData.isEnabled != 'undefined') {
                            if (that.lastPayData.isEnabled == 1) {
                                switch (val) {
                                    case 1:
                                        return '<span style="color: red;">IC卡制卡失败</span>';
                                    case 2:
                                        return '<span>IC卡已开户</span>';
                                    case 0:
                                        return '<span style="color:orangered";>未开户</span>';
                                }
                            }
                            else {
                                return '<span style="color: red;line-height:3.5em">已销户</span>';
                            }
                          }
                        }    
                    }
                }
            },
            //页面显示详细信息
            pageViewData: function () {
            return {
              'customerInfo': [{name: '用户姓名', isShow: true, value: this.lastPayData.customerName},
                {name: '门牌编号', isShow: true, value: this.lastPayData.propertyName},
                {name: '联系电话', isShow: true, value: this.lastPayData.customerContact},
                {name: '用户地址', isShow: true, value: this.lastPayData.customerAddr},
                {name: '透支金额', isShow: true, value: typeof this.lastPayData.overdraftAmount == 'undefined'  ? '--' :  this.lastPayData.overdraftAmount.toString()},
                {name: '告警阀值1', isShow: true, value: this.lastPayData.alarmThreshold || '0'},
                {name: '告警阀值2', isShow: true, value: this.lastPayData.alarmThreshold1 || '0'},
                {name: '告警阀值3', isShow: true, value: this.lastPayData.alarmThreshold2 || '0'},
                {name: '部门机构', isShow: true, value: this.lastPayData.orgName || '--'},
                // {name: '表计户号', isShow: true, value: this.lastPayData.meterUserNo},
                // {name: '电表表号', isShow: true, value: this.lastPayData.electMeterNo},
                // {
                //   name: '电表类型',
                //   isShow: true,
                //   formatFunc: 'formatMeterType',
                //   value: this.lastPayData.meterType
                // },
                // {name: '计费类型', isShow: true, value: this.lastPayData.dictItemName},
                // {
                //   name: '开户状态',
                //   isShow: this.lastPayData.localControl == 1,
                //   formatFunc: 'formatAccount',
                //   value:  this.lastPayData.isAccount
                // },
                // {
                //   name: '费控方式',
                //   isShow: this.lastPayData.localControl == 2,
                //   value: '远程费控'
                // },
                // {
                //   name: '设备状态',
                //   isShow: this.lastPayData.localControl == 2,
                //   formatFunc: 'formatIsOn',
                //   value:  this.lastPayData.isOn
                // }
              ],
              payment: [
                {
                  name: '系统总购电金额', isShow: true,
                  value: typeof this.lastPayData.totalAmount !== 'undefined' ? "￥" + parseFloat(this.lastPayData.totalAmount).toFixed(2) : ""
                },  {
                  name: '系统购电次数', isShow: true,
                  value: typeof this.lastPayData.payCount !== 'undefined' ? "购电[" + this.lastPayData.payCount + "]次" : ""
                }, {
                  name: '电表剩余金额', isShow: true,
                  value: typeof this.lastPayData.remainAmount !== 'undefined' ? "￥" + parseFloat(this.lastPayData.remainAmount).toFixed(2) : ""
                }, {
                  name: '剩余金额采集时间', isShow: true,
                  value: this.lastPayData.amountCollectTime
                },{
                  name: '电表总购电金额', isShow: true,
                  value: typeof this.lastPayData.meterTotalAmount !== 'undefined' ? "￥" + parseFloat(this.lastPayData.meterTotalAmount).toFixed(2) : ""
                },{
                  name: '电表购电次数', isShow: true,
                  value: this.lastPayData.meterPayCount ? "购电[" + this.lastPayData.meterPayCount + "]次" : ""
                }],
              lastPayment: [
                {name: '购电日期', isShow: true, value: this.lastPayData.payDate},
                {
                  name: '购电金额', isShow: true, color: '#ED8A37',
                  value: typeof this.lastPayData.payMoney !== 'undefined' ? "￥" + parseFloat(this.lastPayData.payMoney).toFixed(2) : ""
                },
                {name: '购电方式', isShow: this.lastPayData.localControl != 2, formatFunc: 'formatModel', value: this.lastPayData.payModel},
                //剩余金额
                {
                  name: '账户余额', isShow: this.lastPayData.localControl == 2,
                  value: typeof this.lastPayData.remainAmount !== 'undefined' ? "￥" + parseFloat(this.lastPayData.remainAmount).toFixed(2) : ""
                }, {
                   name: '更新时间', isShow: this.lastPayData.localControl == 2,
                   value: this.lastPayData.amountCollectTime
                },{
                   name: '购电次数', isShow: this.lastPayData.localControl == 2,
                   value: typeof this.lastPayData.payCount !== 'undefined' ? "购电[" + this.lastPayData.payCount + "]次" : ""
                },
                {
                  name: '下发状态',
                  isShow: this.lastPayData.localControl != 2,
                  formatFunc: 'formatState',
                  value: this.lastPayData.writeMeter
                },
                {name: '备注信息', isShow: this.lastPayData.writeMeter === 3, value: this.lastPayData.remark}
              ]
            };
          },
            //打印数据
            printVal: function () {
                let cstName=this.lastPayData.customerName;
                let strTime=this.lastPayData.payDate;

                if(cstName.length>2){
                    cstName= this.lastPayData.customerName.replace(/^(.).+(.)$/g, "$1**$2")
                }
                else{
                    cstName= this.lastPayData.customerName.replace(/^(.).+$/g, "$1*")
                }
                if(strTime.length>0){
                    strTime=strTime.substring(0,16)
                }
              
                return [
                    {ItemType: 0, Filed: "用户名称:", Value:cstName , Align: 0},
                   // {ItemType: 0, Filed: "表计户号:", Value: this.lastPayData.meterUserNo, Align: 0},
                   // {ItemType: 0, Filed: "电表表号:", Value: this.lastPayData.electMeterNo, Align: 0},
                    {ItemType: 0, Filed: "用户地址:", Value: this.lastPayData.customerAddr, Align: 0},
                    {ItemType: 0, Filed: "充值金额:", Value: "￥" + parseFloat(this.lastPayData.payMoney).toFixed(2), Align:0},
                    {ItemType: 0, Filed: "购电日期:", Value: strTime, Align: 0},
					          {ItemType: 0, Filed: "缴费方式:", Value: this.lastPayData.payMethod, Align: 0},
//                  {ItemType: 0, Filed: "缴费种类:", Value: "电费", Align: 0},
                    {ItemType: 0, Filed: "经 办 人:", Value: this.lastPayData.chargeUserName ? this.lastPayData.chargeUserName.substring(0,1) : '', Align: 0},
		                {ItemType: 0, Filed: "流 水 号:", Value: this.lastPayData.serialNum, Align: 0}
                ]
            }
            },
        watch: {
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/main.scss";
    $leftContentH: 1em;
    $leftContentW: 16%;
    $leftContentMinW: 23.5em;
    .detail-container{
      margin-top: 3.3em;
      height: calc(100% - 3em);
      .special-content {
        background:rgba(#3588fa, 0.3);
        padding:0!important;
        .content-left{
          width: $leftContentW;
          font-size: 0.875em;
      //    min-width: $leftContentMinW;
          margin: 1.5em;
          padding: 0.6em 1.25em;
          min-height: $leftContentH;
          border: 0.2em solid #3E8BDB;
          border-radius: 0.4em;
          float:left;
          p.HF-p{
            font-size: 1em;
            text-align:left;
            margin:0;
            i.hasIcon{
              float:right;
              color: #3E8BDB;
              margin-right: 0.5em;
              cursor: pointer;
            }
            i.hasIcon:hover{
              color: white
            }
          }
          p.content-p{
            margin: 2em;
            text-align: center;
            span{
              font-size: 2.7em;
              color: #EE8836;
              margin-right:0.3em;
              font-weight: 600;
            }
          }
        }
        .content-right{
          float:left;
          margin: 1em 1.5em 1em 2.5em;
          width: calc(100% - #{$leftContentW} - 9.9em);
          min-height: calc(#{$leftContentH} + 0.4em + 2.5em);
          .form-list{
            li {
              .label-show{
                min-width:7em
              }
              .operate-div{
                padding: 0;
                margin-left: 2.5em
              }
            }
          }
        }
      }
      .list-content:after,  .form-list:after{
        @extend .clearElement
      }
    }
    .lbstyle {
        margin-top: 8px;
        margin-left: 8px;
        float: left;
        line-height: 2;
    }

    .inputStyle {
        float: left;
        min-width: 20em;
        background: rgba(34, 127, 223, 0.2);
        border:1.5px solid #227fdf;
         padding: 0.8em 0.9em;
         line-height: 1.5;
        // height: 3em;
    }
    .inputStyle:hover{
      border-color: #66afe9;
      box-shadow:  none;
    }

    .buttonStyle {
        float: left;
        margin-left: 10px;
    }

    .lbmoney {
        color: white;
        font-size: 1.2em;
        float: left;
        margin: 3px 10px 0 0;
        width: 10em;
        border-bottom: 1px solid #ccc;
        text-align: center;
        cursor: not-allowed;
        line-height: 2;
    }

    .tabth {
        width: 200px;
        background-color: #f1f1f1;
        color: #008B2E;
        padding: 8px !important;
    }

    .tabtdwidth {
        width: 300px;
    }
    
    .tabtd {
        background-color: #ffffff;
        text-align: left !important;
        padding-left: 10px !important;
    }

    .tablePanel {
        float: none !important;
        width: 100% !important;
        max-width: 100% !important;
        padding: 0px 20px 0px 10px !important;
    }

    .tdremark {
        color: red;
        white-space: inherit;
    }

    .msgtipStyle {
        margin: 10px;
        font-size: 16px;
        text-align: center;
    }
  .showStatusImg{
    float:left;
    width: 8%;
    height: 100%;
    margin-top: 0.5em;
    margin-right: 3em;
    p{
      margin-top:0;
      font-size: 0.875em;
    }
  }
  .result-button{
    margin:0;
    float:right;
    background:transparent;
    text-decoration: underline;
    line-height: 1.5em;
  }
  .result-button:hover{
      color: #27A5FF!important;
  }
  .customerTable{
      .dialogMain{
        padding:0.5em!important;
        max-height: 30em!important;
      }
  }

</style>



