<template>
<div id="flashBox" :style="{opacity: !sign ? 0 : 1, width: !sign ? '1px' : 'auto', height: !sign ? '1px' : 'auto', paddingLeft: !sign ? '0' : '1em'}">
        <object type="application/x-shockwave-flash" id="JsSocket" name="JsSocket"
                data="dist/plugin/bgsocket/BgJsSocket.swf" width="115px"
                height="35px" align="middle">

            <param name="movie" value="dist/plugin/bgsocket/BgJsSocket.swf"/>
            <param name="quality" value="high"/>
            <param name="bgcolor" value="#ccc"/>
            <param name="allowscriptaccess" value="sameDomain"/>
            <param name="allowfullscreen" value="true"/>
            <a class="downLoadFlash" href="http://www.adobe.com/go/getflashplayer" target="_blank" title="下载flash player">
                <!--<img src="http://www.adobe.com/images/shared/download_buttons/get_flash_player.gif" alt="下载Flash"/>-->
              <i class="hasIcon"><icon  style="font-size: 1.5em" name="puzzle-piece"></icon></i>
              下载Flash
            </a>
        </object>
</div>
</template>

<script type="text/javascript">
    import 'vue-awesome/icons/puzzle-piece'
    export default {
        props: {},
        data () {
            return {
                sign: true
            }
        },
        watch: {},
        computed: {},
        methods: {
          getFlashVersion : function() {
            try {
              if(typeof window.ActiveXObject !== 'undefined') {
                return parseInt((new ActiveXObject('ShockwaveFlash.ShockwaveFlash')).GetVariable("$version").split(" ")[1].split(",")[0], 10);
              }else{
                return parseInt(navigator.plugins["Shockwave Flash"].description.split(' ')[2], 10);
              }
            } catch(e){
              return 0;
            }
          }
        },
        mounted () {
          if (this.getFlashVersion() !== 0) {
              this.sign = false;
          }
        }
    }
</script>
<style >
  #flashBox{
    text-align: left;
    padding-left: 5em;
  }
  a.downLoadFlash{
    text-decoration: underline;
    color: #69A4E7
  }
  a.downLoadFlash:hover{
    color: white
  }
  a.downLoadFlash i.hasIcon{
    margin-top: 0.2em;
  }
</style>



