<template >
    <div class="myDialog" >
      <dialogs type="customer" ref="userPass"  v-if="showDialog" width="30em" :top="true">
        <div slot="header">
           用户身份验证
        </div>
        <div slot="content">
            <ul class="dialog-ul" style="padding: 2em 0">
              <li v-for="(item, $index) in fromOpt ">
                <label class="label-show">{{item.label}} </label>
                <inputs  ref="passInput" v-if="item.type == 'input'" :option="item.options"  @enter="getForm"></inputs>
              </li>
              <li v-if="tipMsg" class="littleTips">

                  提示：{{tipMsg}}
              </li>
            </ul>
        </div>
      <!--   <div slot="footer">
          <button @click="moveOn" class="button-default">确定</button>
          <button @click="closeOpe" class="button-default cancel-button">取消</button>
        </div> -->
       </dialogs>
        <dialogs ref="alerts" type="alert"></dialogs>
        <encryptPass ref="encrypt"></encryptPass>
    </div>
</template>
<script>
    import encryptPass from './encryptPass.vue'
    export default{
        data () {
            return {
                submitNumber: 0,
                styleParams: '',
                password: '',
                fromOpt: [
                    {
                        type: 'input',
                        label: '操作口令',
                        options: {
                            top: true,
                            type: 'passwordInput',
                            width: 'calc(100% - 6em)',
                            key: 'password',
                            placeHolder: '',
                            regular: 'password',
                            length: 22
                        }
                    }
                ],
              
                showDialog: false
            }
        },
        components: {
          encryptPass
        },
        props: ['tipMsg'],
        methods: {
          
            closeOpe: function () {
                this.closeDialog();
                this.$emit('closeOperate');
            },
            getForm: function (val) {
               // if(val.val !== this.password) {
                    this.getOption(val, this);
                    if(this.password !== '') {
                         this.moveOn()
                    }
              //  }
               
            },
            moveOn: function () {
                let that = this;
                let alerts = that.$refs.alerts;
                if (that.password === '') {
                    alerts.show({
                        msg: '口令不能为空'
                    })
                } else if (that.$refs.passInput[0].showErr) {
                    alerts.show({
                        msg: '输入密码格式不正确'
                    })
                } else {
                    let pass = this.$refs.encrypt.encryptPass(that.password);
                    that.getRequest({
                        url: 'user/validate',
                        method: 'post',
                        param: {password: pass},
                        success: function () {
                                that.closeDialog();
                                that.$emit('nextOperate');
                        },
                        error: function (data) {
                          that.submitNumber ++;
                          alerts.show({
                            msg: data.message
                          })
                        }
                    }, true)
                }
            },
            closeDialog: function () {
                let that = this;
                that.showDialog = false;
                that.$refs.userPass.close();
            },
            show: function () {
                let that = this;
                that.password = '';
                that.showDialog = true;
                setTimeout(function () {
                  if( that.$refs.userPass) {
                       that.$refs.userPass.show({
                         hasClose: true,
                         action: {
                            cancel: function () {
                                that.closeOpe()
                            }
                        }
                     });
                  }  
                }, 0)
            }
        },
        mounted: function () {
        },
        watch: {}
    }
</script>
<style lang="scss">
    .dialogContent {
        ul.bigForm {
            li {
                width: 100%;
                text-align: center;
                label, input, textarea {
                    float: left;
                    margin: 5px
                }
                label {
                    height: 35px;
                    line-height: 35px;
                    width: 100px;
                    text-align: right;
                }
                label + div.mySelect {
                    margin-left: 5px
                }
                textarea {
                    resize: none;
                    margin: 0;
                    border-radius: 4px
                }
                label.special-label {
                    width: 200px;
                    text-align: left;
                    font-weight: 100;
                }
            }
            li.double-input {
                width: auto;
                float: left;
                margin: 5px 0;
                label + div.mySelect {
                    margin-left: 0
                }
                label {
                    width: 70px;
                    margin: 0;
                }
                label.max-length {
                    width: 110px;
                    margin-right: 5px !important;
                }
                input, div.mySelect {
                    width: 190px;
                    margin: 0
                }
            }
        }
    }
   .littleTips{
    font-size: 0.94em;
    padding: 1em 0 0 0;
    color: #EC3935;
    text-align:left!important
   }
</style>



