        //获取提醒信息
        getTipMsg: function getTipMsg() {
            if (typeof this.lastPayData.writeMeter !== 'undefined' && this.lastPayData.isAccount === 2) {
                this.tipsMesage.isReflsh = false;
                switch (this.lastPayData.writeMeter) {
                    case 0:
                        if (this.lastPayData.payModel === 1) {
                            this.tipsMesage.msg = "[" + this.lastPayData.payDate + "]售电记录,正在后台自动远程下发，请稍后...!";
                            this.tipsMesage.isReflsh = true;
                        } else {
                            this.tipsMesage.msg = "[" + this.lastPayData.payDate + "]售电记录,写卡失败，请先补写到购电卡或远程下发!";
                        }
                        break;
                    case 1:
                        this.tipsMesage.msg = "[" + this.lastPayData.payDate + "]售电记录,充值成功!";
                        this.tipsMesage.color = "green";
                        break;
                    case 2:
                        this.tipsMesage.msg = "[" + this.lastPayData.payDate + "]售电记录,已取消远程下发，请先补写到购电卡或重启远程下发!";
                        break;
                    case 3:
                        this.tipsMesage.msg = "[" + this.lastPayData.payDate + "]售电记录,远程下发失败，请先补写到购电卡或重启远程下发!";
                        break;

                }
            }
        },
